export default {
	data() {
		return {
			gender: ['男性','女性'],
			isInfect: ['有','无'],
			bladder: ['正常或高','低'],
			course: ['<=5年','>5年'],
			incontinence: ['有','无'],
			genderIndex: -1,
			isInfectIndex: -1,
			bladderIndex: -1,
			courseIndex: -1,
			incontinenceIndex: -1,
		}
	},
	methods: {
		selectGender(e) {
			this.genderIndex = e;
		},
		selectInfect(e){
			this.isInfectIndex = e;
		},
		selectBladder(e){
			this.bladderIndex = e;
		},
		selectCourse(e){
			this.courseIndex = e; 
		},
		selectIncontinence(e){
			this.incontinenceIndex = e;
		},
	}
}
